

<script setup>
import { ref, reactive, computed, watch, watchEffect } from 'vue'

const gridColumns = ['attribute_name', 'score_value', 'score_description']
let names = reactive([])
const selected = ref('')
const prefix = ref('')
const protocol_name = ref('')



const filteredNames = computed(() =>
  names.filter((n) =>
    n.name.toLowerCase().includes(prefix.value.toLowerCase())
  )
)

const API_PROTOCOLS_URL = 'http://3.19.225.130/api/v1/protocols/?format=json'

//const API_PROTOCOLS_URL = 'http://localhost:8000/api/v1/protocols/?format=json'
watchEffect(async () => {
  const url = `${API_PROTOCOLS_URL}`
  const protocols = await (await fetch(url)).json();
  protocols.forEach((p) => {
    names.push({ id: p.id, name: p.name });
  });

  //console.log("names" + names);
})

const API_CTSCORES_URL = 'http://3.19.225.130/api/v1/ctscores/?format=json'

//const API_CTSCORES_URL = 'http://localhost:8000/api/v1/ctscores/?format=json'
let ctscores = ref([])
async function fetchScores(id) {
  const url = `${API_CTSCORES_URL}` + "&protocol_id=" + id
  const response = await fetch(url)
  //console.log(response)
  const data = await response.json()
  console.log(data)
  if (data.length > 0) {
    ctscores.value = data[0];
  }

}

watch(selected, (value) => {
  protocol_name.value = value
  ctscores.value.score_attribute_sets=[]
  fetchScores(value)
})

function create() {

}

function update() {
  if (hasValidInput() && selected.value) {
    //const i = names.indexOf(selected.value)
    //names[i] = selected.value = `${last.value}, ${first.value}`
  }
}

function del() {
  if (selected.value) {
    const i = names.indexOf(selected.value)
    names.splice(i, 1)
    selected.value = protocol_name.value
  }
}

function hasValidInput() {
  return protocol_name.value.trim()
}
</script>

<template>
  <div>
    <input v-model="prefix" placeholder="Filter contains" />
  </div>

  <select size="25" v-model="selected">
    <option v-for="name in filteredNames" :key="name.id" :value="name.id">{{ name.name }}</option>
  </select>


  <table>
    <thead>
      <tr>
        <th v-for="col in gridColumns" v-bind:key="col">{{ col }}</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="score in ctscores.score_attribute_sets" v-bind:key="score.id">
        <td v-for="key in gridColumns" v-bind:key="key">{{ score[key] }}</td>
      </tr>
    </tbody>
  </table>

  <div class="buttons">
    <button @click="create">Create</button>
    <button @click="update">Update</button>
    <button @click="del">Delete</button>
  </div>
</template>

